.box {
    background-color: rgba(0, 0, 0, 0.89);
    border-radius: 3px;
    font-family: 'SFUIDisplayLight';
    margin-top: 40px;
}

.inputСontainer {
    position: relative;
    margin-bottom: 25px;
    padding-top: 70px;
}

.inputСontainer h2 {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, 0);
    font-size: 72px;
    color: #fff;
    pointer-event: none;
    transition: all 0.5s ease-in-out;
}

.inputСontainer input {
    border: 0;
    border-bottom: 1px solid #555;
    background: #000;
    width: 100%;
    padding: 8px 0 5px 0;
    font-size: 30px;
    color: #fff;
    text-align: center;
}

.inputСontainer input[type="text"]:focus {
    border: none;
    outline: none;
    border-bottom: 1px solid #e74c3c;
    background: #000;
}

.inputСontainer input:focus~h2,
.inputContainer input:valid~h2 {
    top: -12px;
    font-size: 48px;
    opacity: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px #000 inset !important;
}

input:-webkit-autofill {
    -webkit-text-fill-color: #fff !important;
}

.inputСontainer:before {
    content: '';
    background: linear-gradient(90deg, rgba(0, 0, 0, 1) 0%, rgba(255, 255, 255, 0) 50%, rgba(255, 255, 255, 0) 100%);
    height: 20px;
    position: absolute;
    left: 0;
    bottom: 00px;
    width: 300px;
    z-index: 2;
}

.inputСontainer:after {
    content: '';
    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 50%, rgba(0, 0, 0, 1) 100%);
    height: 20px;
    position: absolute;
    right: 0;
    bottom: 00px;
    width: 300px;
    z-index: 2;
}